<template>
<div class="talk-bg" v-loading="loading">
    <div class="bgs">
      <talkCmp @getmore="getmore2" @pus="Addmessagebystudent2" @dele="deletemessage2" :workMessageInfoDTOList="workMessageInfoDTOList2" :total="totalsi2"  heg="1"/>
    </div>
</div>
</template>

<script>
import {fetchjlistmessage, fetchjaddmessage, fetchtjdeletemessage, fetchListlastteachermessage} from '../../axios/requests'
import talkCmp from '../common/talkCmp.vue'
    export default {
      components:{talkCmp},
        data() {
            return {
              loading:false,
              startId2:0,
              workMessageInfoDTOList2:[],
              totalsi2:0,
            }
        },
        mounted(){
            this.Studentlistmessage2(1)
            this.message()
        },
        methods: {
          message() {
    　　　　setInterval( () => {
              this.messageupdate()
    　　　　}, 5000);
          },
          async messageupdate() {
            if (this.workMessageInfoDTOList2.length==0) {
              var id =0;
            }else{
              var id = this.workMessageInfoDTOList2[this.workMessageInfoDTOList2.length-1].id;
            }
            
            try {
              let response = await fetchListlastteachermessage(id);
              if (response.status.code ==0) {
                var newarray = [];
                for(var i=0;i<response.result.length;i++){
                    var one = {'canDelete': response.result[i].canDelete,
                        'content': response.result[i].content,
                        'createtime':response.result[i].createtime,
                        'id': response.result[i].id,
                        'senderName': response.result[i].creatorName}
                    newarray.push(one)
                }
                this.workMessageInfoDTOList2=this.workMessageInfoDTOList2.concat(newarray)
              }else{
                // this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
            }
          },
            async Addmessagebystudent2(talktext) {//添加讨论消息
                let {id} = this;
                if (!talktext) {
                  this.$message({
                    showClose: true,
                    message: '讨论内容不能为空',
                    type: 'error'
                  });
                  return;
                };
                this.loading = true;
                try {
                  let response = await fetchjaddmessage(talktext);
                  if (response.status.code ==0) {
                    this.$message({
                      showClose: true,
                      message: '发表成功',
                      type: 'success'
                    });
                    this.talktext = '';
                    // this.Studentlistmessage2(1)()
                    this.messageupdate()
                  }else{
                    this.$message.error(response.status.description);
                  }
                } catch (e) {
                  console.log(e);
                } finally {
                  this.loading = false;
                }
              },
              getmore2(pagenum){
                this.Studentlistmessage2(pagenum)
              },
              async Studentlistmessage2(pageNum) {//得到讨论消息
                var size=10;
                if (pageNum==1) {
                  this.workMessageInfoDTOList2=[];
                  this.startId=0;
                  var size=5;
                };
                try {
                  let response = await fetchjlistmessage(pageNum, 5, this.startId2);
                  if (response.status.code ==0) {
                    this.totalsi2 = response.result.total;
                    var newarray = [];
                    for(var i=0;i<response.result.list.length;i++){
                        var one = {'canDelete': response.result.list[i].canDelete,
                            'content': response.result.list[i].content,
                            'createtime':response.result.list[i].createtime,
                            'id': response.result.list[i].id,
                            'senderName': response.result.list[i].creatorName,
                            'senderType': 2}
                        newarray.push(one)
                    }
                     this.workMessageInfoDTOList2=newarray.reverse().concat(this.workMessageInfoDTOList2)
                  }else{
                    this.$message.error(response.status.description);
                  }
                } catch (e) {
                  console.log(e);
                } finally {
                }
              },
              deletemessage2(e){
                this.$confirm('确定要删除吗？', '提示', {
                      type: 'warning'
                  })
                  .then(() => {
                      this.Deletestudentmessage2(e.id, e.index);
                  })
                  .catch(() => {});
              },
              async Deletestudentmessage2(id, index) {//删除讨论消息
                this.loading = true;
                try {
                  let response = await fetchtjdeletemessage(id);
                  if (response.status.code ==0) {
                    this.$message({
                      showClose: true,
                      message: '删除成功',
                      type: 'success'
                    });
                    this.workMessageInfoDTOList2.splice(index, 1);
                  }else{
                    this.$message.error(response.status.description);
                  }
                } catch (e) {
                  console.log(e);
                } finally {
                  this.loading = false;
                }
              },

              viewwork(id){
                this.$openBlank('Sworkdetail',{id});
              },
              async GetteachingbyId() {//得到教学详情
                this.loading = true;
                let {id} = this;
                try {
                  let response = await fetchGetteachingbyId(id);
                  if (response.status.code ==0) {
                    this.items = response.result;
                  }else{
                    this.$message.error(response.status.description);
                  }
                } catch (e) {
                  console.log(e);
                } finally {
                  this.loading = false;
                }
              },
        }
    }

</script>

<style lang='scss' scoped>
    .talk-bg{
        height:100%;
        background:#fff;
    }
    .bgs{
      height:100%;
    }
</style>



